.app__projects {
    flex: 1;
    width: 100%;
    flex-direction: column;
}

.app__projects-filter {
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    flex-wrap: wrap;

    margin: 4rem 0 2rem;

    .app__projects-filter-item {
        padding: 0.5rem 1rem;
        font-weight: 800;
        cursor: pointer;
        transition: all 0.3s ease;
        margin: 0.5rem;
        font-family: var(--font-base);
        
        &:hover{
            color: var(--secondary-color);
            border-bottom: 0.75px solid var(--secondary-color);
        }

        @media screen and (min-width: 2000px) {
            padding: 1rem 2rem;
        }
    }

    .item-active {
        color: var(--secondary-color);
        border-bottom: 0.75px solid var(--secondary-color);
    }
}

.app__projects-portfolio {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;

    .app__project-item {
        width: 270px;
        flex-direction: column;
        margin: 2rem;
        padding: 1rem;
        border: 0.75px solid var(--secondary-color);

        color: black;

        cursor: pointer;
        transition: all 0.3s ease;

        &:hover{
            box-shadow: 0 0 25px rgba(0,0,0,0.2);
        }

        // Add media queries for width
    }
}

.app__project-img {
    width: 100%;
    height: 250px;
    position: relative;

    img {
        width: 100%;
        height: 100%;
        object-fit: cover;
    }

    @media screen and (min-width: 2000px) {
        height: 350px;
    }
}

.app__project-hover {
    position: absolute;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0,0,0,0.5);

    opacity: 0;
    transition: all 0.3s ease;

    div {
        width: 50px;
        height: 50px;
        background-color: rgba(0,0,0,0.5);
        color: var(--white-color);

        margin: 1rem;
        font-family: var(--font-base);
        font-weight: 800;
        cursor: pointer;
        transition: all 0.3s ease;

        svg {
            width: 50%;
            height: 50%;
            color: var(--white-color);
        }
    }
}

.app__project-content {
    padding: 0.5rem;
    width: 100%;
    position: relative;
    flex-direction: column;

    p {
        font-family: var(--font-base);
    }

    h4 {
        margin-top: 1rem;
        line-height: 1.5;
    }

    .app__project-tag {
        position: absolute;
        padding: 0.5rem 1rem;
        background-color: var(--white-color);
        top: -25px;
    }
}